const InstagramBlock = (props) => {

    // const user = `https://graph.instagram.com/${userId}?fields=media_preview&access_token=${accessToken}`;
    const media = `https://graph.instagram.com/me/media?fields=id,caption,media_url,timestamp,username,thumbnail_url,permalink,media_type&access_token=${accessToken}`;

    const [posts, setPosts] = useState([]);
    useEffect(() => {

        axios.get(media)
            .then(response => {
                const {data} = response;
                setPosts([...data.data, uno, dos, {...dos, id: "adsdasdasd"}, {...dos, id: "dasda"}]);
                console.log(data.data);
            })
            .catch(error => {
                if (error.response) {
                    console.log(error.response.data);
                    console.log(error.response.status);
                    console.log(error.response.headers);
                  } else if (error.request) {
                    console.log(error.request);
                  } else {
                    console.log('Error', error.message);
                  }
            });
    }, [media]);

    return <>
        <div className="insta">
            {posts.map((it, key) => {
                return <Card className="insta-item" key={key}>
                    <img src={it.media_url} alt="asdf" />
                    <p>{it.caption}</p>
                </Card>
            })}
        </div>
    </>
}

export default InstagramBlock;
